.expense-item {

    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 2px 8px rgba( 0, 0, 0, 0.25 );
    padding: 0.5rem;
    margin: 1rem 0;
    border-radius: 12px;
    background-color: #4B4B4B;
}

.expense-item__description {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: flex-end;
    flex-flow: column-reverse;



}

.expense-item__price {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: flex-end;
    flex-flow: column-reverse;



}

